'use strict'

const u = {
  $$: (name:string) => window.document.querySelector(name),
  has: Object.prototype.hasOwnProperty,
  get_contextName: (input:any) => input.type === 'checkbox' ? 'checked' : 'value',
  isPrevented: (input:any) => input.type === 'checkbox' || input.type === 'color'
}

export default u;