'use strict';

import quicklightAPI from './quicklightAPI'
import u from './utils'

const keyframes = {

  get_textShadow: (position:any) => {
    let str = '';
    const shadows = quicklightAPI.points[position].shadows,
          len = shadows.length;

    quicklightAPI.points[position].shadows.map((shadow:any, i:number) => {
      if (i + 1 != len) {
        str += shadow.color + ' ' + shadow.x + 'px ' + shadow.y + 'px ' + shadow.blur + 'px, ';
      } else {
        str += shadow.color + ' ' + shadow.x + 'px ' + shadow.y + 'px ' + shadow.blur + 'px';
      }
    });
    return str + ";";
  },

  get_propertiesAt: (position:number, vendor:string) => vendor + 'text-shadow: ' + keyframes.get_textShadow(position),

  set_style: (styleStr:string) => { // append le keyframe généré dans une balise style
    const style:any = window.document.createElement('style'),
          animation:any = window.document.createTextNode(styleStr),
          head:any = u.$$('head'),
          livePreview:any = u.$$('#livePreview');

    if (u.$$('style')) {
      u.$$('style').remove();
    }
    livePreview.style.animationName = 'generated_glitch';
    style.type = 'text/css';
    style.appendChild(animation);
    head.appendChild(style);
  },

  // génère la string keyframe, à appeler dès qu'on à besoin de refresh l'animation
  set_keyFrames: () => {
    let str:string = '',
        i:number = -1;
    const steps:number = quicklightAPI.steps + 1,
          vendors:string[] = ['','-webkit-','-moz-'];

    vendors.map((vendor:string) => {
      str += '@' + vendor + 'keyframes generated_glitch {\n';
      i = -1;
      while (++i < steps) {
        str += i + '% { ' + keyframes.get_propertiesAt(i, vendor) + ' }\n';
      }
    });
    keyframes.set_style(str);
  }

};

export default keyframes;