'use strict';

const drag = { // drag n drop fonctionnel, spécialisé pour déplacer les Points (en bas à droite)

  selected: null,
  xPos: 0, yPos: 0,
  xElem: 0, yElem: 0,

  init: (elem:any) => {
    drag.selected = elem;
    drag.xElem = drag.xPos - drag.selected.cx.baseVal.value;
    drag.yElem = drag.yPos - drag.selected.cy.baseVal.value;
  },

  move: (e:any) => {
    drag.xPos = e.pageX;
    drag.yPos = e.pageY;
    if (drag.selected !== null) {
      drag.selected.cx.baseVal.valueAsString = (drag.xPos - drag.xElem) + 'px';
      drag.selected.cy.baseVal.valueAsString = (drag.yPos - drag.yElem) + 'px';
    }
  },

  remove: () => {
    drag.selected = null;
  }

};

export default drag;