'use strict';

import editorCore from './editorCore'
import u from './utils'
import editor from './editor'

const listeners = {

  get_inputListenerFunc: (input:any, callback:any, controlEvent:any) => (
    (event:any) => {
      if (event && controlEvent) {
        event[controlEvent]();
      }
      callback(listeners.get_contextValue(input));
    }
  ),

  get_contextValue: (input:any) => input.type === 'checkbox' ? input.checked : input.value,

  set_inputListeners: (inputs:any, context:any, type:any) => {
    let input:any;

    for (input in inputs) {
      if (u.has.call(inputs, input)) {
        listeners.set_inputListener(
          inputs[input].input,
          editorCore['set_' + input + context],
          type,
          editor.inputs[input].prevented
        );
      }
    }
  },

  set_inputListener: (input:any, callback:any, type:string, prevented:boolean) => {
    input.addEventListener(
      type,
      listeners.get_inputListenerFunc(
        input,
        callback,
        prevented ? 'preventDefault' : false),
      true
    );
  }

};

export default listeners;