'use strict';

import pointEditor from './pointEditor'
import editor from './editor'
import u from './utils'

const svgUtils = {

  median: null,
  rectMedian: null,
  down: false,
  selection: [0],

  init: () => {
    svgUtils.median = svgUtils.get_domSvg( 'svg');
    svgUtils.rectMedian = svgUtils.get_domSvg( 'rect');
  },

  createLayout: () => {
    svgUtils.rectMedian = svgUtils.createForm('rect', {
      x: '0', y: '0',
      width: '100%', height: '100%',
      fill: 'transparent'
    });
    svgUtils.median.setAttribute('width', '100%');
    svgUtils.median.setAttribute('height', '100%');
    svgUtils.median.appendChild(svgUtils.rectMedian);
    svgUtils.createEmptyRect();
    u.$$('#medianChartRect').appendChild(svgUtils.median);
  },

  rectMouseHover: (e:any) => {
    const id:number = parseInt(e.target.getAttribute('id'));

    if (e.target.getAttribute('fill') === 'transparent') {
      e.target.setAttribute('fill', 'red');
    }
    if (svgUtils.down && svgUtils.selection.indexOf(id) === -1) {
      svgUtils.selection.push(id);
      e.target.setAttribute('fill', 'blue');
    }
  },

  rectMouseLeave: (e:any) => {
    if (e.target.getAttribute('fill') === 'red') {
      e.target.setAttribute('fill', 'transparent');
    }
  },

  rectClick: (e:any) => {
    svgUtils.get_pointAt(svgUtils.selection);
    e.target.setAttribute('fill', 'blue');
    svgUtils.down = false;
  },

  activeDown: (e:any) => {
    svgUtils.down = true;
    svgUtils.selection = [parseInt(e.target.getAttribute('id'))];
    e.target.setAttribute('fill', 'blue');
  },

  createEmptyRect: () => {
    let x:string, i:number = -1;
    const len = 101;

    while (++i < len) {
      x = (i - (0.01 * i)) + '%';
      editor.points[i] = svgUtils.createForm( 'rect', {
        x, y: '0%',
        width: '0.99%', height: '100%',
        stroke: 'grey', 'stroke-width': '1px',
        fill: 'transparent', 'class': 'rectEmpty'
      }, [
        { action: 'mouseover', callback: svgUtils.rectMouseHover },
        { action: 'mouseleave', callback: svgUtils.rectMouseLeave },
        { action: 'mousedown', callback: svgUtils.activeDown },
        { action: 'mouseup', callback: svgUtils.rectClick }
      ]);
      editor.points[i].setAttribute('id', i.toString());
      svgUtils.median.appendChild(editor.points[i]);
    }
  },

  createForm: (formType:any, formOptions:any, callbacks?:any) => {
    let formOption:any,
        item:any = svgUtils.get_domSvg(formType);

    for (formOption in formOptions) {
      if (u.has.call(formOptions, formOption)) {
        item.setAttribute(formOption, formOptions[formOption]);
      }
    }
    if (callbacks) {
      callbacks.map((callback) => {
        item.addEventListener(callback.action, callback.callback);
      });
    }
    return item;
  },

  get_pointAt: (positions:any) => {
    editor.set_allEmptyColorElse(positions);
    pointEditor.init(positions);
    positions.map((position:any) => {
      editor.points[position].setAttribute('fill', 'blue');
    });
  },

  get_domSvg: (type:string) => {
    return document.createElementNS('http://www.w3.org/2000/svg', type);
  }

};

export default svgUtils;