'use strict'

import quicklightAPI from './quicklightAPI'
import drag from './drag'
import keyframes from './keyframes'
import svgUtils from './svgUtils'
import u from './utils'

const pointEditor = {

  area: {},
  points: [],
  positions: [],
  pointsPosition: [],
  clicked: false,
  dragging: false,

  init: (positions:Array<any>) => {
    pointEditor.positions = positions
    if (pointEditor.points.length) {
      pointEditor.clearPositions()
    } else {
      pointEditor.createLayout()
    }
    pointEditor.updateTitle()
    pointEditor.addXYPoints()
    pointEditor.initListenersPoints()
    pointEditor.addCenterPoint()
  },

  clearPositions: () => {
    pointEditor.points.map((point:any, i:number) => {
      pointEditor.pointsPosition[i] = { x: 0, y: 0 }
    })
    u.$$('#colorContainer').innerHTML = ''
    u.$$('#posContainer').innerHTML = ''
    u.$$('#blurContainer').innerHTML = ''
  },

  createLayout: () => {
    pointEditor.area = svgUtils.get_domSvg('svg')
    pointEditor.area.style.height = '100%'
    pointEditor.area.style.width = '100%'
    pointEditor.area.appendChild(
        svgUtils.createForm('rect', {
          x: '0', y: '0',
          width: '100%', height: '100%',
          fill: '#424242'
        })
    )
    u.$$('#pointPreview').appendChild(pointEditor.area)
  },

  updateTitle: () => {
    const positions:any = pointEditor.positions,
          len:number = positions.length

    if (len > 1) {
      u.$$('#pointNumber').innerHTML = positions[0].toString() + ' -> ' + positions[(len - 1)].toString()
    } else {
      u.$$('#pointNumber').innerHTML = positions[0].toString()
    }
  },

  calcNewPos: (x:number, y:number, context:any) => {
    let shadow:any, circle:any = pointEditor.pointsPosition[context]
    const diffX:number = x - circle.x,
          diffY:number = y - circle.y

    circle.x = x
    circle.y = y
    pointEditor.positions.map((position:any, i:number) => {
      shadow = quicklightAPI.points[position].shadows[context]
      if (i === 0) {
        shadow.x += diffX
        shadow.y += diffY
      }
    })
    pointEditor.points.map((point:any, i:number) => {
      shadow = quicklightAPI.points[pointEditor.positions[0]].shadows[i]
      pointEditor.updateView(shadow.x, shadow.y, i)
    })
    keyframes.set_keyFrames()
  },

  calcNewPosEvent: (context:any) => (
      (e:any) => {
        pointEditor.calcNewPos(e.offsetX, e.offsetY, context)
      }
  ),

  addXYPoints: () => {
    pointEditor.set_points()
    pointEditor.set_shadowValues()
    pointEditor.set_templateContainer()
    pointEditor.set_PointsPopover()
  },

  set_templateContainer: () => {
    pointEditor.points.map((point:any, i:number) => {
      pointEditor.addOneShadow(i)
    })
  },

  addOneShadow: (number:number) => {
    u.$$("#colorContainer").innerHTML += u.$$("#colorPoint").innerHTML
    u.$$("#posContainer").innerHTML += u.$$("#posPoint").innerHTML
    u.$$("#blurContainer").innerHTML += u.$$("#blurPoint").innerHTML
    u.$$('#colorPointValue').id += number
    u.$$('#colorPointInput').id += number
    u.$$('#nbColor').innerHTML = number
    u.$$('#nbColor').id += number
    u.$$('#blurPointValue').id += number
    u.$$('#blurPointInput').id += number
    u.$$('#nbBlur').innerHTML = number
    u.$$('#nbBlur').id += number
    u.$$('#nbPoint').innerHTML = number
    u.$$('#nbPoint').id += number
    u.$$("#posPointValue").id += number
  },

  set_points: () => {
    let i:number = -1
    const len:number = quicklightAPI.shadowsDefaultNumber

    if (pointEditor.points.length) {
      pointEditor.removePoints()
    }
    while (++i < len) {
      pointEditor.set_point(i)
    }
  },

  set_point: (position:number) => {
    pointEditor.pointsPosition[position] = { x: 0, y: 0 }
    pointEditor.points[position] = svgUtils.createForm('circle', {
      cx: '50%', cy: '50%', r: '0.5em',
      stroke: '#424242', 'stroke-width': '1px',
      fill: 'black', id: 'circle' + position
    }, [
      {action: 'mouseup', callback: pointEditor.calcNewPosEvent(position)}
    ])
    pointEditor.area.appendChild(pointEditor.points[position])
  },

  removePoints: () => {
    pointEditor.points.map((point:any) => {
      pointEditor.area.removeChild(point)
    })
  },

  set_shadowValues: () => {
    setTimeout(function() {
      pointEditor.points.map((point:any, i:number) => {
        pointEditor.set_shadowValue(i)
      })
    }, 0)
  },

  set_shadowValue: (position:number) => {
    const point:any = pointEditor.points[position],
          pointPos:any = pointEditor.pointsPosition[position],
          shadow:any = quicklightAPI.points[pointEditor.positions[0]].shadows[position],
          xBaseVal:any = point.cx.baseVal, yBaseVal = point.cy.baseVal

    xBaseVal.value = (xBaseVal.value + shadow.x)
    yBaseVal.value = (yBaseVal.value + shadow.y)
    point.setAttribute('r', point.r.baseVal.value + "px")
    pointPos.x = xBaseVal.value
    pointPos.y = yBaseVal.value
    pointEditor.calcNewPos(pointPos.x, pointPos.y, position)
  },

  set_changeInputColor: (id:string) => (
      (e:any) => {
        pointEditor.positions.map((position:number) => {
          quicklightAPI.points[position].shadows[id].color = e.target.value
        })
        keyframes.set_keyFrames()
      }
  ),

  set_PointsPopover: () => {
    pointEditor.points.map((point:any, i:number) => {
      const shadow:any = quicklightAPI.points[pointEditor.positions[0]].shadows[i],
            color:any = u.$$("#colorPointInput" + i),
            blur:any = u.$$("#blurPointInput" + i)

      color.value = shadow.color
      color.onchange = pointEditor.set_changeInputColor(i.toString())
      blur.value = shadow.blur
      blur.onchange = pointEditor.set_changeInputBlur(i.toString())
    })
  },

  set_changeInputBlur: (id:string) => (
      (e:any) => {
        pointEditor.positions.map((position:number) => {
          quicklightAPI.points[position].shadows[id].blur = e.target.value
        })
        keyframes.set_keyFrames()
      }
  ),

  updateView: (x:number, y:number, i:number) => {
    u.$$("#posPointValue" + i).innerHTML = x + ' ' + y
  },

  initListenersPoints: () => {
    pointEditor.points.map((point:any, i:number) => {
      const shadow = u.$$('#circle' + i)
      shadow.onmousedown = pointEditor.circleMouseDown
      shadow.onmousemove = pointEditor.circleMouseMove
      shadow.onmouseup = pointEditor.circleMouseUp
      shadow.onclick = pointEditor.circleOnClick
    })
  },

  addCenterPoint: () => {
    pointEditor.area.appendChild(
        svgUtils.createForm('circle', {
          cx: '50%', cy: '50%', r: '0.1em',
          stroke: '#424242', fill: 'red'
        })
    )
  },

  circleMouseDown: (e:any) => {
    pointEditor.clicked = true
    drag.init(e.target)
    return false
  },

  circleMouseMove: (e:any) => {
    drag.move(e)
    if (pointEditor.clicked) {
      pointEditor.dragging = true
    }
  },

  circleMouseUp: (e:any) => {
    if (!pointEditor.dragging) {
      e.target.setAttribute('stroke', 'blue')
      e.target.setAttribute('stroke-width', '3px')
      // select pointEditor circle and init point
    }
  },

  circleOnClick: () => {
    pointEditor.dragging = false
    pointEditor.clicked = false
    drag.remove()
  }

}

export default pointEditor