'use strict';

import quicklightAPI from './quicklightAPI'
import keyframes from './keyframes'
import listeners from './listeners'
import svgUtils from './svgUtils'
import u from './utils'

const editor = {

  values: {},
  inputs: {},
  points: new Array(101),
  contextTypeEvents: {
    Display: 'input',
    Value: 'change'
  },

  inputNames: {
    'animationDuration': {type: 'range'},
    'hideMedianText': {type: 'checkbox'},
    'textColor': {type: 'color'},
    'color': {type: 'color'},
    'point2point': {type: 'checkbox'}
  },

  init: () => {
    editor.init_values()
    editor.init_inputs()
    editor.init_inputListeners()
    editor.init_svg()
    editor.init_keybinding()
    editor.init_keyframe()
  },

  init_values: () => {
    quicklightAPI.set_Points()
  },

  init_inputs: () => {
    let name:string,
        input:any,
        display:any

    for (name in editor.inputNames) {
      if (u.has.call(editor.inputNames, name)) {
        editor.inputs[name] = {
          input: u.$$('#' + name + 'Input'),
          display: u.$$('#' + name + 'Value'),
          prevented: u.isPrevented(editor.inputNames[name])
        }
        input = editor.inputs[name].input
        input[u.get_contextName(input)] = quicklightAPI[name]
        display = editor.inputs[name].display
        display.innerHTML = quicklightAPI[name]
      }
    }
  },

  init_svg: () => {
    svgUtils.init()
    svgUtils.createLayout()
    svgUtils.rectClick({target: editor.points[0]})
  },

  init_keyframe: () => {
    keyframes.set_keyFrames()
  },

  init_inputListeners: () => {
    let context:string

    for (context in editor.contextTypeEvents) {
      if (u.has.call(editor.contextTypeEvents, context)) {
        listeners.set_inputListeners(
            editor.inputs,
            context,
            editor.contextTypeEvents[context]
        )
      }
    }
  },

  set_allEmptyColorElse: (positions:Array<any>) => {
    editor.points.map((point:any, i:number) => {
      positions.map((position:number) => {
        if (i !== position) {
          point.setAttribute('fill', 'transparent');
        }
      })
    })
  },

  init_keybinding: () => {
    // window.addEventListener('keydown', e => {
    //   switch (e.keyCode) {
    //     case 37: // left
    //       break;
    //     case 39: // right
    //       break;
    //   }
    // })
  }

};

export default editor