'use strict';

import quicklightAPI from './quicklightAPI'
import keyframes from './keyframes'
import u from './utils'

const editorCore = {

  set_animationDurationValue: (animationDuration:number) => {
    quicklightAPI.animationDuration = animationDuration + 'ms';
    editorCore.set_styleProperty('#livePreview', 'animationDuration', animationDuration + 'ms');
  },

  set_animationDurationDisplay: (animationDuration:number) => {
    editorCore.set_property('#animationDurationValue', 'innerHTML', animationDuration + 'ms');
  },

  set_hideMedianTextValue: (hideMedianText:boolean) => {
    quicklightAPI.hideMedianText = hideMedianText;
    editorCore.set_styleProperty(
      '#livePreview',
      'color',
      (hideMedianText ?
        'transparent' :
        quicklightAPI.textColor)
    );
  },

  set_hideMedianTextDisplay: (hideMedianText:boolean) => {
    editorCore.set_property('#hideMedianTextValue', 'innerHTML', hideMedianText);
  },

  set_textColorDisplay: (textColor:string) => {
    editorCore.set_property('#textColorValue', 'innerHTML', textColor);
  },

  set_textColorValue: (textColor:string) => {
    quicklightAPI.textColor = textColor;
    editorCore.set_styleProperty('#livePreview', 'color', textColor ? textColor : 'white');
  },

  set_colorValue: (color:string) => {
    quicklightAPI.color = color;
    editorCore.set_majorColor(color);
  },

  set_colorDisplay: (color:string) => {
    editorCore.set_property('#colorValue', 'innerHTML', color);
  },

  set_point2pointValue: (point2point:boolean) => {
    quicklightAPI.point2point = point2point;
    // add point2point feature
  },

  set_point2pointDisplay: (point2point:boolean) => {
    editorCore.set_property('#point2pointValue', 'innerHTML', point2point);
  },

  set_property: (element:any, name:string, value:any) => {
    u.$$(element)[name] = value;
  },

  set_styleProperty: (element:any, name:string, value:any) => {
    u.$$(element).style[name] = value;
  },

  set_majorColor: (color:string) => {
    quicklightAPI.points.map((point:any) => {
      point.shadows.map((shadow:any) => {
        shadow.color = color;
      });
    });
    keyframes.set_keyFrames();
  }

};

export default editorCore;